import { useState, useRef } from 'react'; 
import styled from 'styled-components';

function Guest() {
    let [guest, changeGuest] = useState(['안녕하세요', '하이']);
    let [txt, changeTxt] = useState('');
    let [like, setLike] = useState([0, 0]);

    return (
        <>
            <div className="guestBox">
                <div className="guestInputBox">
                <input type="text" onChange={(e) => {
                    changeTxt(e.target.value);
                    console.log(txt);
                }}
                />
                <button className="writeBtn" onClick={() => {
                    let copy = [...guest];
                    let plus = 0;
                    like.unshift(plus);
                    copy.unshift(txt);
                    changeGuest(copy);
                }}>작성</button>
                </div>
                <ul className="guestList">
                {
                    guest.map(function(a, i){
                        return (
                            <li key={i}>
                                <p className="guestTxt">{ guest[i] }</p>
                                <div className="guestBtn">
                                    <button className="likeBtn" onClick={() => {
                                        let copyLike = [...like];
                                        copyLike[i] = copyLike[i] + 1;
                                        setLike(copyLike)}}>👍
                                    </button>
                                    { like[i] }
                                </div>
                            </li>
                        )
                    })

                }
                </ul>
            </div> 
        </>
    )
}

export default Guest;