import { useState, useRef } from 'react'; 
import styled from 'styled-components';
import work from '../db/work.json';
import { motion } from 'framer-motion';

let HistoryBox = styled.div`
    margin-top: 30px;
    width: calc(100% - 80px);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-sizing: border-box;
`
let HistoryBoxIn = styled.div`
    display: flex;
    flex-direction: row;
    border-top: 4px solid #555;
    border-bottom: 1px solid #000;
`
let Year = styled.ul`
    width: 10%;
    list-style-type: none;
    text-align: left;
    font-family: "Libre Bodoni", serif;;
    font-weight: bold;
    font-size: 40px; 
    margin: 0;
    border-right: 1px solid #000;
    padding-top: 12px;
`

let WorkList = styled.li`
    border-top: ${(props) => props.borderTop};
    list-style-type: none;
    text-align: center;
    border-top: 1px solid #555;
    padding: 20px 10px;
    width: calc(100% / 4);
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    box-sizing: border-box;
    text-align: left;
    &:nth-of-type(-n + 4) {
        border-top: none;
    }
`
let WorkPoster = styled.img`
    width: 100%;
    height: 100%;
`
let WorkTextBox = styled.div`
    padding-top: 20px;
`
let WorkTitle = styled.p`
    font-family: 'Noto Sans Korean';
    font-size: 18px;
    margin: 0;
`
let WorkDate = styled.p`
    font-family: 'Nanum Myeongjo';
    font-size: 16px;
    margin: 0 0 8px;
`
let WorkName = styled.p`
    font-family: 'Nanum Myeongjo';
    font-size: 26px;
    font-weight: bold;
    margin: 0 0 18px;
`
let WorkDia = styled.p`
    font-family: 'Nanum Myeongjo';
    font-size: 16px;
    margin: 0;
`
const variants = {
    hidden: { opacity: 0 },
    visible: { opacity: 1 }
  }

function History() {
    let year = ['ALL', 2023, 2022, 2021, 2020, 2019];
    const [yearFilter, setYearFilter] = useState(work.works);
    const [activeIndex, setActiveIndex] = useState(null);  

    return (
        <HistoryBox>
            <p className="workBoxTitle">Filmography</p>
            <HistoryBoxIn>
                <Year>
                    {
                        year.map(function(a, i) {
                            return (
                                <li key={i}
                                    className={activeIndex === i ? 'year on' : 'year'}
                                    onClick={() => {
                                        setActiveIndex(i);
                                        const filtered = work.works.filter((data) => {
                                            console.log('choiceYear = ' + data.start.split(".")[0]);
                                            return data.start.split(".")[0] == a
                                        });
                                    {
                                        a === 'ALL' ? setYearFilter(work.works) : setYearFilter(filtered);
                                    }
                                }}>{a}</li>
                            )
                        })
                        
                    }
                </Year>
                <div className="workBox">
                    
                    <ul className="workBoxIn">
                        {
                            yearFilter.map(function(a, i) {
                                let yearList = a.start.split(".")[0];
                                console.log('yearList = ' + yearList);
                                console.log(yearFilter);

                                return (
                                    <WorkList key={i}>
                                        <div className="workPosterBox">
                                            <motion.img className="workImg" src={'./work' + a.id + '.png'} 
                                            initial={{ opacity: 1 }} 
                                            whileHover={{ opacity: 0 }}/>
                                            <WorkPoster src={'./poster' + a.id + '.png'} />
                                        </div>
                                        <WorkTextBox>
                                            <div className="historyDetail">
                                                <WorkTitle>{a.title}</WorkTitle>
                                                <WorkDate>{a.start} ~ {a.end}</WorkDate>
                                                <WorkName>{a.name}</WorkName>
                                            </div>
                                            <WorkDia>"{a.dialogue}"</WorkDia>
                                        </WorkTextBox>
                                    </WorkList>
                                )
                            })
                        }
                    </ul>
                </div>
            </HistoryBoxIn>
        </HistoryBox>
    )
}

export default History;