import { useState, useRef, useEffect } from 'react' 
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation } from 'swiper/modules';
import { motion, useAnimationControls, AnimatePresence } from 'framer-motion';
import 'swiper/css';
import 'swiper/css/navigation';

import styled from 'styled-components';
import work from '../db/work.json';

let SwiperCard = styled.div`
  padding: 20px;
`
let NaviBtn = styled.span`
  font-size: 22px;
`
let Ticket = styled.div`
  width: 350px;
  height: 690px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
`
let TicketCon = styled.div`
  position: relative;
  height: 500px;
`
let ConBg = styled.img`
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
  width: 100%;
  height: 100%;
  z-index: -1;
`
let TicketInfo = styled.div`
  color: ${props => props.컬러};
`

let TicketTitle = styled.pre`
  font-family: "Libre Bodoni", serif;;
  font-size: 45px;
  font-weight: bold;
  text-align: center;
  line-height: 95%;
  margin: 60px 0 12px;
  text-shadow: 4px 3px 5px rgba(0,0,0,0.2);
`
let TicketStory = styled.p`
  width: 70%;
  margin: 0 auto;
  font-family: "Libre Bodoni", serif;;
  font-size: 14px;
  font-weight: bold;
`
let WorkDetail = styled.div`
  font-family: "Libre Bodoni", serif;;
  position: absolute;
  bottom: 15px;
  left: 18px;
  text-align: left;
  font-weight: bold;
  color: ${props => props.컬러};
`
let CutBox = styled.div`
  background-color: ${props => props.컬러};
  height: 190px;
  border-top: 2px dashed #f8f1e7;
  font-family: 'Nanum Myeongjo';
  padding-left: 18px;
  flex-shrink: 0;
`

function Recently() {
    const prevRef = useRef(null);
    const nextRef = useRef(null);
    const imgRoot = process.env.PUBLIC_URL;
    let [dia, setDia] = useState(0);
    let [workPop, setWorkPop] = useState(false);
    const [selectedShow, setSelectedShow] = useState({});
    const reverse = [...work.works].reverse();
    console.log(selectedShow);
    console.log(reverse);

    const openPop = (e) => {
      setWorkPop((workPop) => !workPop);
    }
    const controls = useAnimationControls();
 
    useEffect(() => {
      if (!workPop) {
        controls.start({ y: "20%" });
      }
    }, [controls, workPop]);

    return (
        <>
            <div className="swiperArea">
              <div className="swiperTitle">
                <p>Recently</p>
                <pre className="workDia">
                  <img src={imgRoot + '/text.png'} width="70" className="openQuote" />
                  {reverse[dia].dialogue}
                  <img src={imgRoot + '/close.png'} width="70" className="closeQuote" />
                </pre>
                <div className="btnBox">
                  <NaviBtn ref={ prevRef }>&lt; prev</NaviBtn>
                  <NaviBtn ref={ nextRef }>next &gt;</NaviBtn>
                </div>
              </div>
              <Swiper
                slidesPerView={3}
                modules={[Navigation]}
                onSlideChange={(swiper) => {
                  console.log(swiper.activeIndex);
                  setDia(swiper.activeIndex);
                  console.log(work.works[dia].dialogue);
                }}
                onSwiper={(swiper) => console.log(swiper)}
                onInit={(swiper) => {
                  swiper.params.navigation.prevEl = prevRef.current;
                  swiper.params.navigation.nextEl = nextRef.current;
                  swiper.navigation.init();
                  swiper.navigation.update();
                }}          
              >
                {
                  reverse.map(function(a) {
                    return (
                      <SwiperSlide className="workSlide">
                        <SwiperCard className="workCard"
                        onClick={() => {
                          setSelectedShow(a);
                        }}>
                          <a href="javascript:void(0)" onClick={() => openPop()}>
                            <img src={'./work' + a.id + '.png'} />
                          </a>
                        </SwiperCard>
                      </SwiperSlide>
                    )
                  })
                }
              </Swiper>
            </div>
            <AnimatePresence>
            {
              workPop ? (
                <motion.div className="workDetail" key={selectedShow.id} exit={{opacity: 0}} transition={{delay: 0.5}}>
                  <Ticket>
                    <TicketCon>
                      <ConBg src={'./bg' + selectedShow.id + '.png'} />
                      <TicketInfo 컬러={selectedShow.color}>
                        <TicketTitle>{selectedShow.eng}</TicketTitle>
                        <TicketStory>{selectedShow.info}</TicketStory>
                      </TicketInfo>
                      <WorkDetail 컬러={selectedShow.color}>
                        <p className="theaterName">{selectedShow.place}</p>
                        <span className="workDate">{selectedShow.start} ~ {selectedShow.end}</span>
                      </WorkDetail>
                    </TicketCon>


                      <motion.div  animate={controls} >
                        <CutBox 컬러={selectedShow.color} onClick={() => {
                          controls.start({ y: "20%", rotate: '-7deg' });
                          setWorkPop(false);
                        }}>
                          <p class="showTitle">{selectedShow.title}</p>
                          <div>
                            <span>{selectedShow.name}</span>
                            <span>황순종</span>
                          </div>
                        </CutBox>
                      </motion.div>



                    
                  </Ticket>
                </motion.div>
              ) : (
                <></>
              )
            }
            </AnimatePresence>
        </>
    )
}

export default Recently;